import { FC } from "react";

import { Button, Flex, IconButton, useBreakpointValue } from "@chakra-ui/react";

import { ArrowRightIcon, GitHubIcon, LinkIcon, FigshareIcon, YoutubeIcon, PaperIcon } from "utils/Icons";
import { open } from "utils/Functions";
import { Text} from "@chakra-ui/react";

interface GitHubButtonProps {
    github?: string;
    display?: any;
}

interface FigshareButtonProps {
    figshare?: string;
    display?: any;
}

interface PaperButtonProps {
    paper?: string;
    display?: any;
}

interface ReadMoreProps {
    readMore?: string;
}

interface LiveDemoProps {
    demo?: string;
    display?: any;
}
interface ArticleProps {
    article?: string;
    display?: any;
}

interface Props extends GitHubButtonProps, ReadMoreProps, LiveDemoProps, ArticleProps, FigshareButtonProps, PaperButtonProps {}

export const ReadMore: FC<ReadMoreProps> = ({ readMore }) => {
    return readMore ? (
        <Button
            data-aos="fade"
            data-aos-offset="200"
            variant="link"
            colorScheme="black"
            mt='3'
            rightIcon={<ArrowRightIcon fontSize="16pt" />}
            onClick={() => open(readMore)}
        >
            Read More
        </Button>
    ) : null;
};

export const GitHubButton: FC<GitHubButtonProps> = ({ github, display }) => {
    const as = useBreakpointValue({ base: IconButton, lg: Button });

    return github ? (
        <Button
            variant="secondary"
            size="sm"
            mt="2"
            leftIcon={<GitHubIcon />}
            onClick={() => open(github)}
            px={{ base: 2, lg: 3 }}   
            minW="auto"
            gap={{ base: 0, lg: 2 }}
            sx={{
                "& .chakra-button__icon": {
                // leftIcon이 만들어두는 오른쪽 마진 제거
                marginInlineEnd: { base: 0, lg: "0.5rem" },
                },
            }}     // 모바일 폭 줄이기
            >
            <Text as="span" display={{ base: "none", lg: "inline" }}>
            github
            </Text>
            </Button>
        
    ) : null;
};
