import { FC, useMemo, useState } from "react";
import {
  Box, Button, Checkbox, CheckboxGroup, Divider, Flex, Heading, Input, 
  InputGroup, InputLeftElement, SimpleGrid, Stack, Text, useColorModeValue, 
  useDisclosure, Drawer, DrawerOverlay, DrawerContent, DrawerHeader, 
  DrawerCloseButton, DrawerBody, Image, Modal, ModalOverlay, ModalContent, 
  ModalHeader, ModalCloseButton, ModalBody, ModalFooter
} from "@chakra-ui/react";

import { ChevronDownIcon, ChevronUpIcon, SearchIcon } from "utils/Icons"; 

import { configs } from "shared/content/Content";
import { ProjectCardFooter } from "shared/project-card-footer/ProjectCardFooter";
import { Tags } from "shared/tags/Tags";

type Project = (typeof configs)["otherProjects"][number];

const TAG_ORDER = ["Conversational Agent", "Eye Tracking", "LLM Application", "AR/VR", "Visualization"];
const initialCount = 6;
const incrementor = 6;

export const OtherProjects: FC = () => {
  const [searchTerm, setSearchTerm] = useState("");
  const [selectedStatuses, setSelectedStatuses] = useState<string[]>([]);
  const [selectedTags, setSelectedTags] = useState<string[]>([]);
  const [count, setCount] = useState(initialCount);
  const [activeProject, setActiveProject] = useState<Project | null>(null);
  
  const detailModal = useDisclosure();
  const mobileFilter = useDisclosure();
  const projects = configs.otherProjects as Project[];

  // 1. 필터링 핵심 로직 (General Standard)
  const filtered = useMemo(() => {
    return projects.filter((p) => {
      // 검색어 필터
      const matchesSearch = p.title.toLowerCase().includes(searchTerm.toLowerCase()) || 
                            (p as any).description?.toLowerCase().includes(searchTerm.toLowerCase());
      
      // 상태 필터 (아무것도 선택 안 하면 Pass, 선택하면 포함 여부 확인)
      const matchesStatus = selectedStatuses.length === 0 || selectedStatuses.includes(p.status);
      
      // 태그 필터 (아무것도 선택 안 하면 Pass, 선택하면 하나라도 포함된지 확인)
      const ptags = (p.tags as string[]) || [];
      const matchesTag = selectedTags.length === 0 || ptags.some(t => selectedTags.includes(t));

      return matchesSearch && matchesStatus && matchesTag;
    });
  }, [projects, searchTerm, selectedStatuses, selectedTags]);

  const ongoing = filtered.filter(p => p.status === "ongoing");
  const previous = filtered.filter(p => p.status === "previous");

  const clearAll = () => {
    setSearchTerm("");
    setSelectedStatuses([]);
    setSelectedTags([]);
  };

  return (
    <Box maxW="1200px" mx="auto" px={4}>
      {/* 상단 검색 및 모바일 필터 바 */}
      <Flex gap={4} mb={8} direction={{ base: "column", md: "row" }}>
        <InputGroup size="lg">
          <InputLeftElement pointerEvents="none">
            <SearchIcon />
            </InputLeftElement>
          <Input 
            placeholder="Search by keywords..." 
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
            bg={useColorModeValue("white", "gray.800")}
            borderRadius="full"
          />
        </InputGroup>
        
        <Button 
          display={{ base: "flex", md: "none" }}
          // leftIcon={<FilterIcon />}
          onClick={mobileFilter.onOpen}
          size="sm"
          px={8}
          borderRadius="full"
          // colorScheme="teal"
        >
          Filters {(selectedStatuses.length + selectedTags.length) > 0 && `(${selectedStatuses.length + selectedTags.length})`}
        </Button>
      </Flex>

      <Flex gap={10} align="flex-start">
        {/* 데스크탑 사이드바 필터 */}
        <Stack 
          display={{ base: "none", md: "flex" }} 
          w="260px" 
          position="sticky" 
          top="100px" 
          spacing={8}
        >
          <Box>
            <Flex justify="space-between" align="center" mb={4}>
              <Text fontWeight="800" fontSize="lg">Filters</Text>
              <Button size="xs" variant="link" onClick={clearAll} color="teal.500">Reset All</Button>
            </Flex>
            
            <Stack spacing={6}>
              {/* Status Section */}
              <Box>
                <Text fontSize="xs" fontWeight="bold" color="gray.500" mb={3} textTransform="uppercase">Status</Text>
                <CheckboxGroup value={selectedStatuses} onChange={(v) => setSelectedStatuses(v as string[])}>
                  <Stack spacing={2}>
                    <Checkbox value="ongoing" colorScheme="teal">Ongoing ({projects.filter(p => p.status === "ongoing").length})</Checkbox>
                    <Checkbox value="previous" colorScheme="teal">Previous ({projects.filter(p => p.status === "previous").length})</Checkbox>
                  </Stack>
                </CheckboxGroup>
              </Box>

              <Divider />

              {/* Tags Section */}
              <Box>
                <Text fontSize="xs" fontWeight="bold" color="gray.500" mb={3} textTransform="uppercase">Expertise</Text>
                <CheckboxGroup value={selectedTags} onChange={(v) => setSelectedTags(v as string[])}>
                  <Stack spacing={2}>
                    {TAG_ORDER.map(t => (
                      <Checkbox key={t} value={t} colorScheme="teal">
                        {t}
                      </Checkbox>
                    ))}
                  </Stack>
                </CheckboxGroup>
              </Box>
            </Stack>
          </Box>
        </Stack>

        {/* 프로젝트 그리드 영역 */}
        <Box flex="1">
          {filtered.length === 0 ? (
            <Flex direction="column" align="center" justify="center" py={20}>
              <Text fontSize="xl" fontWeight="bold" color="gray.400">No projects found matching your filters.</Text>
              <Button mt={4} variant="link" color="teal.500" onClick={clearAll}>Clear all filters</Button>
            </Flex>
          ) : (
            <>
              {ongoing.length > 0 && (
                <Box mb={12}>
                  <Heading size="md" mb={6} display="flex" alignItems="center">
                    Ongoing <Box as="span" ml={2} fontSize="sm" color="gray.400" fontWeight="normal">({ongoing.length})</Box>
                  </Heading>
                  <SimpleGrid columns={{ base: 1, sm: 2, lg: 3 }} spacing={6}>
                    {ongoing.map(p => <QuickProjectCard key={p.id} project={p} onOpenDetail={(p) => { setActiveProject(p); detailModal.onOpen(); }} />)}
                  </SimpleGrid>
                </Box>
              )}

              {previous.length > 0 && (
                <Box>
                  <Heading size="md" mb={6} display="flex" alignItems="center">
                    Previous <Box as="span" ml={2} fontSize="sm" color="gray.400" fontWeight="normal">({previous.length})</Box>
                  </Heading>
                  <SimpleGrid columns={{ base: 1, sm: 2, lg: 3 }} spacing={6}>
                    {previous.slice(0, count).map(p => <QuickProjectCard key={p.id} project={p} onOpenDetail={(p) => { setActiveProject(p); detailModal.onOpen(); }} />)}
                  </SimpleGrid>
                  
                  {previous.length > count && (
                    <Flex mt={10} justify="center">
                      <Button variant="outline" borderRadius="full" px={10} rightIcon={<ChevronDownIcon />} onClick={() => setCount(c => c + incrementor)}>
                        Load More Projects
                      </Button>
                    </Flex>
                  )}
                </Box>
              )}
            </>
          )}
        </Box>
      </Flex>

      {/* 모바일 필터 Drawer */}
      <Drawer isOpen={mobileFilter.isOpen} onClose={mobileFilter.onClose} placement="bottom">
        <DrawerOverlay />
        <DrawerContent borderTopRadius="3xl" p={4}>
          <DrawerHeader borderBottomWidth="1px" display="flex">
            Filters
            <Button size="sm" variant="ghost" onClick={clearAll}>Reset</Button>
          </DrawerHeader>
          <DrawerBody py={6}>
            <Stack spacing={8}>
              <Box>
                <Text fontWeight="bold" mb={4}>Status</Text>
                <CheckboxGroup value={selectedStatuses} onChange={(v) => setSelectedStatuses(v as string[])}>
                  <Flex gap={4} flexWrap="wrap">
                    <Checkbox value="ongoing">Ongoing</Checkbox>
                    <Checkbox value="previous">Previous</Checkbox>
                  </Flex>
                </CheckboxGroup>
              </Box>
              <Box>
                <Text fontWeight="bold" mb={4}>Tags</Text>
                <CheckboxGroup value={selectedTags} onChange={(v) => setSelectedTags(v as string[])}>
                  <SimpleGrid columns={2} spacing={3}>
                    {TAG_ORDER.map(t => <Checkbox key={t} value={t}>{t}</Checkbox>)}
                  </SimpleGrid>
                </CheckboxGroup>
              </Box>
              <Button colorScheme="teal" size="lg" w="100%" onClick={mobileFilter.onClose} borderRadius="xl">Show {filtered.length} Projects</Button>
            </Stack>
          </DrawerBody>
        </DrawerContent>
      </Drawer>

      {/* 프로젝트 상세 Modal (기존 코드와 동일하게 유지) */}
      <Modal isOpen={detailModal.isOpen} onClose={detailModal.onClose} isCentered size="xl">
        <ModalOverlay backdropFilter="blur(5px)" />
        <ModalContent borderRadius="2xl" maxH="90vh" overflow="hidden">
          <ModalHeader borderBottomWidth="1px">{activeProject?.title}</ModalHeader>
          <ModalCloseButton />
          <ModalBody p={6} overflowY="auto">
            {activeProject && (
              <Stack spacing={5}>
                <Image src={activeProject.image} borderRadius="xl" />
                <Flex gap={2}>{activeProject.tags.map(t => <Box key={t} px={2} py={1} bg="gray.100" borderRadius="md" fontSize="xs" fontWeight="bold">{t}</Box>)}</Flex>
                <Text lineHeight="tall">{activeProject.description}</Text>
                <ProjectCardFooter {...(activeProject as any)} />
              </Stack>
            )}
          </ModalBody>
          <ModalFooter borderTopWidth="1px"><Button onClick={detailModal.onClose}>Close</Button></ModalFooter>
        </ModalContent>
      </Modal>
    </Box>
  );
};

// --- Sub Component: Project Card ---
const QuickProjectCard: FC<{ project: Project; onOpenDetail: (p: Project) => void }> = ({ project, onOpenDetail }) => {
  const cardBg = useColorModeValue("white", "gray.800");
  const cardBorder = useColorModeValue("gray.200", "gray.700");

  return (
    <Box
      onClick={() => onOpenDetail(project)}
      border="1px solid" borderColor={cardBorder}
      borderRadius="2xl" bg={cardBg} overflow="hidden"
      transition="all .2s" _hover={{ transform: "translateY(-5px)", boxShadow: "2xl", cursor: "pointer" }}
    >
      <Box position="relative" pt="60%">
        <Image src={project.image} alt={project.title} position="absolute" inset={0} w="100%" h="100%" objectFit="cover" />
      </Box>
      <Box p={5}>
        <Text fontWeight="bold" fontSize="md" mb={2} noOfLines={1}>{project.title}</Text>
        <Tags tags={project.tags} size="xs" />
      </Box>
    </Box>
  );
};